import Vue from 'vue'
import VueRouter from 'vue-router'
import cams from '../views/allCam.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Cams',
    component: cams
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
